.App {
  font-family:Avenir, "Century Gothic", sans-serif;
  font-weight:100; 
  font-style:normal;
  font-size: calc(10px + 2vmin);
  color: white;
  justify-content: center;
  background-color: rgba(104, 6, 6, 0.962);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.header{
  width: 100%;
  color:rgb(0, 0, 0);
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: baseline;
  background-color: #fff564;
  margin-bottom: 20px;
}

.headerBtn {
  margin-bottom: 50px;
  margin-right: 10px;
}

.ReadPosts {
  display: block;
  width:100%;
}

button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 15px;
  font-weight: 500;
  font-family: inherit;
  background-color: #1a1a1a;
  color:white;
  cursor: pointer;
  transition: border-color 0.25s;
}
button:hover {
  border-color: #646cff;
}

p{
  font-size: 15px;}

.searchBar{
  width:500px;
  max-width:90%;
}
