.Card {
    display:block;
    margin:0;
    backdrop-filter: blur(2px);
    border: 1px solid rgba(153, 153, 153, 0.497);
    border-radius: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-bottom: 25px;
    width: 1200px;
    max-width: 90%;
    text-align: left;
    padding-left: 30px;
}

.title {
    color:white;
    font-size: 24px;
}

.content {
    color:white;  
    text-align: justify;
    text-justify: inter-word;
    padding-right: 30px;
    margin-bottom: 20px;
    font-size: 15px;
}

.betButton {
    border-radius: 8px;
    border: 1px solid transparent;
    padding: 0.6em 1.2em;
    font-size: 15px;
    font-weight: 500;
    font-family: inherit;
    background-color: #1a1a1a;
    color:white;
    cursor: pointer;
    transition: border-color 0.25s;
}
.betButton:hover {
    border-color: #646cff;
  }

.moreButton {
    width: 30px;
    float:right;

}
.moreButton:hover {
    cursor: pointer;
}